  width: 100%;
  background-color: white;
  color: #000000;
}

.presentation-container {
  width: 100%;
  height: 100%;
  display: flex;
}

/*------------------------------------------------------------------------
* LEFT PART STARTS
--------------------------------------------------------------------------*/
