}

/*------------------------------------------------------------------------
* LEFT PART ENDS
--------------------------------------------------------------------------*/
/*------------------------------------------------------------------------
* RIGHT PART STARTS
--------------------------------------------------------------------------*/
.presentation__right {
  display: flex;
  place-items: center;
  width: 50%;
}
.presentation__right img {
  max-width: 90%;
}

/*------------------------------------------------------------------------
* RIGHT PART ENDS
--------------------------------------------------------------------------*/
@media screen and (max-width: 56.5625rem) {
  .section-presentation {
    height: 100vh;
    padding-bottom: 30px;
  }

  .presentation-container {
    flex-direction: column;
  }

  .presentation__left {
    height: 60%;
    width: 100%;
    justify-content: flex-start;
  }

  .presentation__right {
    height: 40%;
    width: 100%;
    justify-content: center;
  }

  .presentation__right img {
    height: 100%;
    width: 90%;
  }

  .presentation__header {
    width: 70%;
  }

  .presentation__body {
    font-size: 1.125rem;
    width: 70%;
  }

  .presentation-cta__button {
    left: 20%;
  }
}
/* ----- Just a little line up effect ----------- */
.line-up {
  animation: 2s animate-lineUp ease-out;
}

@keyframes animate-lineUp {
  0% {
    opacity: 0;
    transform: translateY(100%);
  }
  20% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  80% {
    opacity: 1;
    transform: translateY(0%);
  }
  100% {
    opacity: 1;
    transform: translateY(0%);
  }
}
