.material-dsgn-bar {
  position: relative;
  border-bottom: 0.063rem solid #999999;
  display: block;
}

.material-dsgn-bar::before {
  content: "";
  height: 0.125rem;
  width: 0;
  left: 50%;
  bottom: -0.063rem;
  position: absolute;
  background: var(--violet-hocarre);
  transition: left 0.28s ease, width 0.28s ease;
  z-index: 2;
}

.input-data,
.contact-textarea {
  display: block;
  background: none;
  padding: 0.125rem 0.125rem 0.063rem;
  font-size: 1rem;
  border-width: 0;
  border-color: transparent;
  line-height: 1.8;
  width: 100%;
  color: transparent;
  transition: all 0.31s ease;
  box-shadow: none;
}

.input-data:focus,
.input-data:valid,
.contact-textarea:focus,
.contact-textarea:valid {
  color: #333333;
}

.input-data:focus ~ .f-ctrl-label,
.input-data:valid ~ .f-ctrl-label,
.contact-textarea:focus ~ .f-ctrl-label,
.contact-textarea:valid ~ .f-ctrl-label {
  font-size: 0.8rem;
  top: -1rem;
  left: 0;
}

.input-data:focus,
.contact-textarea:focus {
  outline: none;
}

.input-data:focus ~ .f-ctrl-label,
.contact-textarea:focus ~ .f-ctrl-label {
  color: var(--violet-hocarre);
}

.input-data:focus ~ .material-dsgn-bar::before,
.contact-textarea:focus ~ .material-dsgn-bar::before {
  width: 100%;
  left: 0;
}

.ct-form-btn {
  position: relative;
  background-color: var(--violet-hocarre);
  border: 1px solid var(--violet-hocarre);
  font-size: 1rem;
  color: var(--violet-hocarre);
  margin: 1.5rem 0 1rem 0;
  padding: 0.5rem 1.8rem;
  cursor: pointer;
  overflow: hidden;
  color: var(--white);
  border-radius: 0.4375rem;
}

